import { mapState } from 'vuex'
import moment from 'moment'
import 'moment/locale/zh-cn'
moment.locale('zh-cn')

export default {
	data() {
		return {
			Overview: {},
			pageNum: 0,
			curPage: 1,
			drawerHidden: true,
			postData: [],
			StatusBar: this.StatusBar,
			CustomBar: this.CustomBar,
			isloading: true
		}
	},
	filters: {
		formatTime(value) {
			return moment.unix(value).fromNow()
		}
	},
	computed: {
		...mapState(['authorInfo'])
	},
	methods: {
		seePost(cid) {
			wx.navigateTo({
				url: './post?cid=' + cid
			})
		},
		showDrawer(e) {
			this.drawerHidden = false
		},
		hideDrawer(e) {
			this.drawerHidden = true
		},
		async initPost(pageOffset) {
			this.postData = await this.$api.getRecentPost({ page: pageOffset })
			this.isloading = false
		},
		navPage(page) {
			if (page <= 0 || page > this.pageNum) return
			this.isloading = true
			this.curPage = page
			this.drawerHidden = this.initPost(page)
		}
	},
	async onShow() {
		const _this = this
		this.initPost(1)
		_this.Overview = await this.$api.getOverview()
	},
	async onLoad(options) {
		this.pageNum = await this.$api.getPageNum()
	}
}
